.App {
  font-family: Arial, sans-serif;
  text-align: center;
  margin: 20px;
}

.input-container {
  margin-bottom: 20px;
}

input {
  padding: 8px;
  margin-right: 10px;
  width: 300px;
}

button {
  padding: 8px 15px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.table-container {
  margin-top: 20px;
  overflow-x: auto;
}

table {
  margin: 0 auto;
  border-collapse: collapse;
  width: 80%;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f4f4f4;
}

.error {
  color: red;
  margin-top: 20px;
}

.spinner {
  margin-top: 20px;
  font-size: 16px;
  color: #007bff;
}
